button { 
    display: table-cell;
    text-align: center;
    outline: none;
    background-color: var(--light);
    height: "50%";
    font-family: "Lexend Deca", sans-serif;
    font-size: 3rem;
    font-weight: 600;
    color: var(--text);
    background-size: 100% 100%;
    border-radius:5%;
    margin-bottom: 15px;
    margin-right: 2%;
    margin-left: 2%;
    
  }

  .btn{
    height: 50%;
    width: 15%;
  }
  


button:active {
    background-image: linear-gradient(
      145deg,
      transparent 10%,
      var(--dark) 10% 20%,
      transparent 20% 30%,
      var(--dark) 30% 40%,
      transparent 40% 50%,
      var(--dark) 50% 60%,
      transparent 60% 70%,
      var(--dark) 70% 80%,
      transparent 80% 90%,
      var(--dark) 90% 100%
    );
    animation: background 3s linear infinite;
  }
  
  @keyframes background {
    0% {
      background-position: 0 0;
    }
    100% {
      background-position: 400px 0;
    }
  }

