@import url('https://fonts.googleapis.com/css2?family=Electrolize&display=swap');


* {
  margin: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  
}
.imgbox {
  display: grid;
  height: 100%;
}
center-fit {
  max-width: 100%;
  max-height: 100%;;
  margin: auto;
}

  :root {
    --background: #4174ae;
    --background-accent: #0b1c38;
    --background-accent-2: #1d488f;
    --light: #1d488f;
    --dark: #0b1c38;
    --text: #ffffff;
    height: 99vh;
    align-items: stretch;
  }

  body {    
    background-image: url('../../assets/images/Logo_Watermark_2048x1536.png');
    font-family: Helvetica;
    -webkit-font-smoothing: antialiased;
    height: 100%;
    width: auto;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-repeat: no-repeat;
    background-size: 100% 30px;
    background-position: top left, bottom left;
    min-height: 98vh;
  }
  
  div {
    /* position: relative; */
    width: 100%;
    height: 100%;
    table-layout: inherit;
    margin: 0 auto 0 auto;
    left: 0;
    right: 0;
    top: 10%;
  }

  h1{
    font-family: 'Electrolize', sans-serif;
    padding-top: 2%;
    line-height: 1.205;
    color: var(--dark);
    border-color: black;
    font-size: 7vh

  }


  .addBlock{
    display: block;
    text-align: center;
  }

  .btn{
    text-align: center;
    height: auto;
    width: 15%;
  }
  