@import url('https://fonts.googleapis.com/css2?family=Electrolize&display=swap');


strong{   
    font-family: 'Electrolize', sans-serif;
    color: whitesmoke;
    vertical-align: inherit;
    font-family: 'Electrolize', sans-serif;
    padding-bottom: 10%;
    line-height: 1.205;
}
.text-small {
    font-size: 0.9rem !important;
}

.header {
    background-color: var(--background);
    /* height: 14%; */
}

.section-1 {
    background: #f5f5f5;
}

.section-2 {
    background: #F1F1F1;
}

.section-3 {
    background: #EAEAEA;
}

.section-4 {
    background: #E5E5E5;
}

img{
    height: 10%;
    width: auto;
    align-self: auto;
    padding-right: 2%;
}

h1{
    vertical-align: middle;
    font-family: 'Electrolize', sans-serif;
    padding-bottom: 10%;
    line-height: 1.205;
    color: var(--dark);
    border-color: black;
    
  }

  
  .navbar.navbar-inverse {
    border: none;
  }
  .navbar .navbar-brand {
    padding-top: 0px;
  }
  .navbar .navbar-brand img {
    height: 50px;
    min-height: 50px;
  }

  .footer{
    /* position: absolute; */
