body{
    background: var(--background);
    background-image: url('../../assets//images/Logo_Watermark_2048x1536.png');

}

h2{
    text-align: center;
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: white;
    padding: 30px 0;
}

/* Table Styles */

.table-wrapper{
    box-shadow: 0px 35px 50px rgba( 0, 0, 0, 0.2 );
    /* height: auto; */
}

input[type=checkbox]{
    -webkit-transform: scale(2);
}


table {
    align-items: stretch;
    border-radius: 5px;
    font-size: 150%;
    font-weight: normal;
    border: none;
    border-collapse: collapse;
    width: 100%;
    height: 0.5vh;
    max-width: 100vw;
    max-height: 0.5vh;
    /* overflow: scroll; */
    /* white-space: nowrap; */
}

 td,  th {
    text-align: center;
    max-width: 50%;
    padding: 8px;
    overflow-wrap:break-word;
    white-space: unset;
    overflow: hidden;
}
 td {   
    border-right: 1px solid #f8f8f8;
    font-size: 150%;
}

 thead th {
    color: #ffffff;
    background: var(--background);
    position: sticky;
    table-layout: fixed;
    /* display: block; */
}

tbody{
    overflow: scroll;
    width: 100vw;
    height: 40vh;
    table-layout: fixed;
    display: block;
}

tr{
    width: 100vw;
    display: table;
    table-layout: fixed;
}

/* td:first-child { width: 20% ;} */

 thead th:nth-child(odd) {
    color: #ffffff;
    background: #324960;
}

 tr:nth-child(even) {
    background: #F8F8F8;
}

.table-container {
    /* overflow: auto; */
    /* max-height: 160px; */
    border: 1px solid red;
  }

