
  .content{
    width: 98%;
    height: auto;
    margin: 0 auto;
    padding: 5%;
  }

  ul{
    list-style-type: none;
  }

  .nav-pills{
    width: 98%;
    height: 100px; 
    padding-left: 2%;
  }
  .nav-item{
    width: 50%;
  }
  .nav-pills .nav-link{
    font-weight: bold;
    text-align: center;
    background: #343436;
    color: whitesmoke;
    border-radius: 30px;
    height: 75px;
  }
  .nav-pills .nav-link.active{
    background: whitesmoke;
    color: #000;
  }
  .tab-content{
    display: block;
    position: relative;
    align-items: center;
    align-content: center;

    width: 98%;
    height: auto;
    margin-top: -50px;
    background: whitesmoke;
    color: #000;
    border-radius: 30px;
    z-index: 1000;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.4);
    padding: 30px;
    padding-top: 5%;
    margin-bottom: 50px;
  }

  .tab-content label{
      color: black;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
  
  li {
    float: left;
    box-sizing: content-box;
  }
  
  li a {
    display: block;
    color: white;
    text-align: center;
    padding: 16px;
    text-decoration: none;
    font-size: x-large;
  }
  
  