label{
  text-align: center;
    padding-left: 1%;
    font-size: 250%;
    color: var(--dark);
  }

  input[type=text]{
    text-align: center;
    padding-left: 1%;
    padding-top: 1%;
    padding-bottom: 1%;
    margin: 7px;
    font-size: 50%;
    background:#86aff0;
    border: none;
    transition: background 0.5s;
    font-size:70%;
  }

  input[type=email]{
    text-align: center;
    padding-left: 1%;
    padding-top: 1%;
    padding-bottom: 1%;
    margin: 7px;
    background:#86aff0;
    border: none;
    transition: background 0.5s;
    font-size: 70%;
  }


  
  input[type=number]{
    text-align: center;
    padding-left: 10px;
    padding-top: 1%;
    padding-bottom: 3px;
    margin: 7px;
    font-size: 50%;
    background: #86aff0;
    border: none;
    transition: background 0.5s;
    font-size: 70%;
  }

input:focus{
      outline-width: 0.5;
      background: rgba(31,134,252, 0.15);
      transition: background 0.5s;
    }

input[type=submit]{
  text-align: center;
  color: black;

}